.containerDiv {
  text-align: center;
}

.inputBox {
  border: 0;
  border-bottom: 2px solid black;
  width: 50%;
  font-size: 30px;
  line-height: 35px;
  height: 70px;
}

.xboxify {
  display: inline-block;
  padding: 1rem;
  border-radius: 2em;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 1rem;
  color: #ffffff;
  background-color: black;
  text-align: center;
}

.gamertag {
  margin-top: 1rem;
  display: block;
  font-size: 2rem;
  overflow-wrap: break-word;
  max-width: 100rem;
}
